.sidebar {
    width: 250px;
    height: 100%;
    background-color: #f8f8f8;
    border-right: 1px solid #e0e0e0;
    display: flex;
    flex-direction: column;
}

.list {
    list-style: none;
    padding: 0;
    margin: 0;
    flex-grow: 1;
    overflow-y: auto;
}

.item {
    padding: 10px;
    cursor: pointer;
    border-bottom: 1px solid #e0e0e0;
}