.select {
    appearance: none;
    background-color: #fff;
    background-image: url('data:image/svg+xml;charset=utf-8,<svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 24 24"><path d="M7 10l5 5 5-5z" fill="%233f3f3f"/></svg>');
    background-position: right 0.5rem center;
    background-repeat: no-repeat;
    background-size: 1rem 1rem;
    border: 1px solid #2d2d2d;
    border-radius: 4px;
    color: #2d2d2d;
    font-size: 16px;
    padding: 8px 40px 8px 16px;
}
  
.select:focus {
    border-color: #3d3d3d;
    outline: none;
 }