.taskControl {
    display: flex;
    flex-wrap: wrap;
    gap: 20px;
    font-size: 16px;
    font-weight: bold;
    background-color: white;
    box-shadow: 0px 3px 3px rgba(0, 0, 0, 0.25);
    padding: 20px;
}
  
.assignee,
.priority,
.labels {
    display: flex;
    align-items: center;
    gap: 10px;
}

.label {
    color: #6b778c;
}

.value {
    color: #172b4d;
}

.labelList {
    display: flex;
    flex-wrap: wrap;
    gap: 5px;
}

.labelItem {
    background-color: #ebecf0;
    color: #172b4d;
    font-size: 12px;
    padding: 2px 4px;
    border-radius: 3px;
}

.title {
    font-size: 24px;
    margin-bottom: 20px;
    width: 100%;
}

.wrapper{
    transition: 0.5s;
}