.dropdown {
    position: relative;
}

.toggle {
    cursor: pointer;
    padding: 10px;
    display: flex;
    justify-content: space-between;
}

.menu {
    position: absolute;
    top: 100%;
    left: 0;
    background-color: white;
    border: 1px solid #dfe1e6;
    border-radius: 3px;
    max-height: 400px;
    width: max-content;
    overflow-y: auto;
}

.option {
    cursor: pointer;
    padding: 10px;
    font-size: 13px;
    display: flex;
    justify-content: start;
}

.option:hover {
    background-color: #f4f5f7;
}