.header{
  margin: 10px 0;
  text-align: center;
}

.content{
  background-color: #ffff;
  padding: 10px 26px;
  overflow: auto;
}

.row{
  display: flex;
  flex-direction: row;
}

.column{
  display: flex;
  flex-direction: column;
}

.gap{
  gap: 20px;
}

.margin-right > *{
  margin-right: 16px;
}

.alignRight{
  align-items: flex-end;
}

.alignLeft{
  align-items: flex-start;
}

.alignCenter{
  align-items: center;
}

.justRight{
  justify-content: end;
}

.justLeft{
  justify-content: start;
}

.justCenter{
  justify-content: center;
}

.justBetween{
  justify-content: space-between;
}

.displayNone{
  display: none;
}

.errorPanel{
  background-color: #dba1a1;
  font-size: 17px;
}

.successPanel{
  background-color: #aed5a4;
  font-size: 17px;
}

.warrningPanel{
  background-color: #d5d1a4;
  font-size: 17px;
}

.errorMessage{
  color: #d66363;
  font-size: 17px;
}

.successMessage{
  color: #7ad663;
  font-size: 17px;
}

.widthFull {
  width: 100%;
}

.heightFull {
  height: 100%;
}

.flexBasisFull{
  flex-basis: 100%;
}

.wrap {
  flex-wrap: wrap;
}

.transition{
  transition: 1s;
}
  