.taskColumn {
    background-color: #f5f5f5;
    padding: 20px;
    border-radius: 5px;
    box-shadow: 0px 3px 3px rgba(0, 0, 0, 0.25);
    width: 300px;
    display: flex;
    flex-direction: column;
    gap:10px;
}

.taskColumnTitle {
  font-size: 24px;
  font-weight: bold;
  display: flex;
  gap:10px;
  justify-content: space-between;
  align-items: center;
}

.taskList {
  display: flex;
  flex-direction: column;
  gap: 10px;
}