.button {
    background-color: #2d2d2d;
    border: none;
    border-radius: 4px;
    color: #fff;
    cursor: pointer;
    font-size: 16px;
    padding: 8px 16px;
    transition: background-color 0.2s ease;
  }
  
  .button:hover {
    background-color: #3d3d3d;
  }
  
  .button:disabled {
    opacity: 0.6;
    cursor: not-allowed;
  }